body {
  position: relative;
  z-index: -80;
  margin: 0px;
  font-family: sans-serif;
}
#root {
  position: relative;
  z-index: -80;
}
a {
  text-decoration: none;
  color: white;
  font-size: 14px;
  font-weight: bold;
}

.btnGreen {
  background-color: #00ba94;
  border-radius: 25px;
  border: none;
  color: white;
  margin: 0px;
  padding: 13px 27px;
  font-size: 14px;
  font-weight: bold;
}
.btnGreen:hover {
  cursor: pointer;
}

.outer {
  position: absolute;
  height: 800px;
  width: 200%;
  border-bottom-left-radius: 50%;
  border-bottom-right-radius: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.section {
  position: relative;
  height: 800px;
  overflow: hidden;
  display: flex;
  justify-content: center;
}

.background-purple {
  background-image: linear-gradient(135deg, #0061f2, rgba(148, 74, 212, 1));
}

.background-white {
  background-color: white;
}
.background-lightgray {
  background-color: #eff3f9;
}

.position-after-1 {
  position: relative;
  margin-top: -400px;
  z-index: -1;
}
.position-after-2 {
  position: relative;
  margin-top: -125px;
  z-index: -2;
}
.position-after-3 {
  position: relative;
  margin-top: -175px;
  z-index: -3;
}
.topSection {
  height: 100px;
  width: 100%;
}

.projects > div {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.projectPreviewDark {
  box-sizing: border-box;
  position: relative;
  height: 210px;
  width: 300px;
  object-fit: cover;
  border-radius: 2px;
  transition: 0.2s;
  box-shadow: 0 0 rgb(43, 55, 168);
  margin-left: 10px;
}

.projectPreviewDark:hover {
  transform: translateX(7px) translateY(-7px);
  box-shadow: -7px 7px 0px rgb(43, 55, 168), -6px 6px 0px rgb(43, 55, 168),
    -5px 5px 0px rgb(43, 55, 168), -4px 4px 0px rgb(43, 55, 168),
    -3px 3px 0px rgb(43, 55, 168), -2px 2px 0px rgb(43, 55, 168),
    -1px 1px 0px rgb(43, 55, 168), 0px 0px 0px rgb(43, 55, 168);
  transition: ease 0.2s;
}
.projectPreviewRed {
  box-sizing: border-box;
  position: relative;
  height: 210px;
  width: 300px;
  object-fit: cover;
  border-radius: 2px;
  transition: 0.2s;
  box-shadow: 0 0 orangered;
  margin-left: 10px;
}

.projectPreviewRed:hover {
  transform: translateX(7px) translateY(-7px);
  box-shadow: -7px 7px 0px orangered, -6px 6px 0px orangered,
    -5px 5px 0px orangered, -4px 4px 0px orangered, -3px 3px 0px orangered,
    -2px 2px 0px orangered, -1px 1px 0px orangered, 0px 0px 0px orangered;
  transition: ease 0.2s;
}
.projectPreviewBlack {
  box-sizing: border-box;
  position: relative;
  height: 210px;
  width: 300px;
  object-fit: cover;
  border-radius: 2px;
  transition: 0.2s;
  box-shadow: 0 0 black;
  margin-left: 10px;
}

.projectPreviewBlack:hover {
  transform: translateX(7px) translateY(-7px);
  box-shadow: -7px 7px 0px black, -6px 6px 0px black, -5px 5px 0px black,
    -4px 4px 0px black, -3px 3px 0px black, -2px 2px 0px black,
    -1px 1px 0px black, 0px 0px 0px black;
  transition: ease 0.2s;
}

i {
  font-size: 20px;
  color: white;
  font-family: "FontAwesome";
  font-style: normal;
}
.inner {
  position: relative;
  display: flex;
  align-items: flex-start;
}

.links {
  position: absolute;
  right: -45px;
}

.links > a {
  background-color: #00ba94;
  padding: 5px 6px;
  border-radius: 100%;
  margin: 5px 0px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.underline {
  position: relative;
}

.underline::after {
  overflow: hidden;
  position: absolute;
  left: 0;
  bottom: -5px;
  content: "";
  height: 2px;
  width: 100%;
  background-color: white;
  transition: 0.2s;
  animation: grow 0.5s;
}

@keyframes grow {
  0% {
    width: 0px;
    transform: rotate(0deg);
  }

  20% {
    transform: rotate(-90deg);
    width: 20%;
  }

  40% {
    transform: rotate(-180deg);
    width: 40%;
  }

  60% {
    transform: rotate(-270deg);
    width: 60%;
  }

  80% {
    transform: rotate(-360deg);
    width: 80%;
  }
  100% {
    transform: rotate(-360deg);
    width: 100%;
  }
}
