.footer-wrapper {
  position: relative;
  margin-top: -75px;
  z-index: -4;
  height: 350px;
  width: 100%;
  display: flex;
  flex-direction: column;
  color: #687281;
}

.footer-data .contact-links > i {
  color: #687281;
  font-size: 30px;
}
.footer-data .contact-links > a > i {
  color: #687281;
  font-size: 30px;
}
.footer-data {
  margin-top: 75px;
  margin-left: 175px;
  margin-right: 175px;
  border-bottom: 1px solid #687281;
}

.contact-links {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 175px;
  margin-bottom: 50px;
}

.bottom-footer {
  margin-left: 175px;
  margin-right: 175px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.bottom-footer i {
  color: #687281;
}

.about-web {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 155px;
}

.about-web > div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 80px;
}
